.client-home {
    text-align: center;
    padding: 20px;
}

.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px; /* Space between the images */
    flex-wrap: wrap; /* Ensures responsiveness */
    padding: 20px 0;
}

.review-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;
}

.client-image {
    width: 100px; /* Adjust the size as needed */
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
}

.client-name {
    margin-top: 10px;
    font-size: 16px;
    color: #333;
}

.read-more-container {
    margin-top: 20px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .home-container {
        gap: 10px; /* Reduce space between images on smaller screens */
    }

    .client-image {
        width: 80px;
        height: 80px;
    }

    .client-name {
        font-size: 14px;
    }
}


