/* src/App.css */
@keyframes float {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0px);
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .logo {
    width: 100px;
    height: auto;
    margin-bottom: 20px;
    animation: float 3s ease-in-out infinite;
  }
  
  .review-card {
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    animation: fadeIn 1s ease-in-out;
  }
  