
.project-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 20px;
    gap: 20px;
    background-color: #f9f9f9;
    font-family: 'Arial', sans-serif;
}

.project-list {
    flex: 1;
    max-width: 300px;
    margin-right: 20px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.project-list h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

.project-list ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.project-list li {
    cursor: pointer;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin: 5px 0;
    text-align: center;
    transition: background-color 0.3s, transform 0.3s;
    background-color: #f0f0f0;
}

.project-list li:hover {
    background-color: #e0e0e0;
    transform: scale(1.05);
}

.project-details {
    flex: 3;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    animation: fadeIn 1s ease-in-out;
}

.project-details h2 {
    margin-bottom: 15px;
    text-align: center;
    color: #555;
}

.project-image {
    width: 50%; /* Reduced to 50% of the original size */
    height: auto;
    border-radius: 10px;
    margin-bottom: 20px;
    transition: transform 0.5s ease-in-out, filter 0.5s ease; /* Add filter transition */
    display: block;
    margin-left: auto;
    margin-right: auto;
    filter: grayscale(100%); /* Start with grayscale */
}

.project-image:hover {
    transform: scale(1.05);
    filter: grayscale(0%); /* Remove grayscale on hover */
}

.animated-image {
    animation: slideIn 1s ease-in-out;
}

.project-details p {
    margin: 10px 0;
    color: #666;
    line-height: 1.6;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideIn {
    from { transform: translateX(-100px); opacity: 0; }
    to { transform: translateX(0); opacity: 1; }
}

/* Responsive design */
@media (max-width: 768px) {
    .project-container {
        flex-direction: column;
        align-items: center;
    }

    .project-list {
        max-width: 100%;
        margin-bottom: 20px;
    }

    .project-details {
        max-width: 100%;
    }

    .project-image {
        width: 70%; /* Adjust for smaller screens */
    }
}
