.product-container {
    position: relative;
    padding: 20px;
}

.logo-container {
    position: absolute;
    top: 20px;
    right: 20px;
}

.logo {
    width: 100px;
    height: auto;
    border-radius: 10px;
}

.product-list {
    margin-top: 100px;
}

.product-list ul {
    list-style-type: none;
    padding: 0;
}

.product-list li {
    cursor: pointer;
    padding: 10px;
    background-color: #f0f0f0;
    margin-bottom: 10px;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.product-list li:hover {
    background-color: #dcdcdc;
}

.product-details {
    margin-top: 20px;
}

.product-details h2 {
    margin-bottom: 10px;
}

.product-details p {
    font-size: 16px;
    color: #333;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    position: relative;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    max-width: 90%;
    max-height: 90%;
}

.modal-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    cursor: pointer;
}
